	module.exports = {"data":{"markdownRemark":{"html":"<p>   The area to the west of the southern portion of Lake Galarand possesses\nmany soft limestone caverns and tunnels. Several of these carry water that has\ndrained from the woodlands to the west, accumulating in the depression\nassociated with Lake Galarand and its neighbouring wetlands. Underground\nstreams and damp caves have encouraged a unique flora and fauna to evolve.\nThese caves have also provided an important refuge for the dark beasts and\nfoul creatures that fled from bowels of Amon Lanc when the army of Loth Lorien\nbeseiged Dol-Guldur.  Probably the most legendary of these creatures is one of\nthe fell beasts of Dol-Guldur, now dubbed 'the fell beast of Mor Erlond'. Most\nof its kin were slaughtered at the end of the third age when the elven army of\nCeleborn marched on Dol-Guldur. The fell beast is believed to prey upon\ntravellers that use the stretch Mor Erlond that runs through here (Mor Erlond\nis a forest pathway which  stretches from Dol-Guldur northwards towards the\nOld Forest Road).</p>\n<p>Aeglos, Sage and Scribe of Loth Lanath.</p>","frontmatter":{"title":"The Fell Beast of Mor Erlond"}}},"pathContext":{"path":"/books/fellbeastofmorerlond"}}

